@import './laoder';
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 1.2em;
  font-family: 'Noto Serif', serif;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2em;
}

.section-heading {
  text-transform: uppercase;
  font-size: 2em;
  color: #28a9e2;
  margin-bottom: 0.5em;
}


.table-box {
  width: 60%;
  height: 75vh;
  background: #fff;
  overflow-y: scroll;
}

table {
  width: 100%;
  box-shadow: 0 0 10px rgba(0,0,0,0.6);
  -moz-box-shadow: 0 0 10px rgba(0,0,0,0.6);
  -webkit-box-shadow: 0 0 10px rgba(0,0,0,0.6);
  -o-box-shadow: 0 0 10px rgba(0,0,0,0.6);
}

table, th, td, input[type="text"] {
  border: 1px solid #1f1f1f;
  border-collapse: collapse;
  text-align: center;
  padding: 0.5em 1em;
}

table tr:nth-child(even) {
  background: #d3d3d3;
  color: #000;
}

th {
  position: sticky;
  top: 0;
  background: #28a9e2;
  color: #fff;
  text-transform: capitalize;
  padding: 1em 2em;
  
}

td:hover {
  cursor: pointer;
}

input[type="text"] {
  width: 100%;
  height: 3em;
  padding: 0;
  margin: 0;
  font-size: 1em; 
}

td.changing {
  padding: 0;
 
}

.action-buttons .success, .action-buttons .danger {
  width: 50%;
  color: #fff;
  border: none;
  height: 1.7em;
  cursor: pointer;
  z-index: 9999;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1em;
}

.action-buttons button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.success {
  background: #00ff00;
}

.danger {
  background: #f32013;
}

.msg-box {
  color:#fff;
  font-weight: 700;
  
  opacity: 0;
  
  text-align: center;
  width: 50%;
  margin-bottom: 1em;
  padding: 0.5em 1em; 
  transition: opacity 1s ease-in-out;
}






